body {
   background: #101010;
   display: flex;
   align-items: center;
   justify-content: center;
}
.App {
   text-align: center;
   height: calc(100svh - 150px);
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: flex-start;
   padding-top: 150px;
   gap: 20px;
   max-width: 500px;
}
.todo__logo {
   color: #3b5bc2;
   letter-spacing: -9.5px;
   text-transform: uppercase;
   font-weight: 900;
   display: flex;
   gap: 10px;
}
.todo__logo > h1 {
   font-weight: 900;
}
