.todo-list {
   width: 100%;
   display: flex;
   flex-direction: column;
   gap: 10px;
   height: 40svh;
   overflow-y: auto;
}
.todo-list::-webkit-scrollbar {
   width: 20px;
}
/* Track */
.todo-list::-webkit-scrollbar-track {
   background: #101010;
}

/* Handle */
.todo-list::-webkit-scrollbar-thumb {
   background: #888;
   border: 5px solid #101010;
   border-radius: 20px;
}

/* Handle on hover */
.todo-list::-webkit-scrollbar-thumb:hover {
   background: #555;
}
.todo-list__empty {
   color: white;
}
