.todo-item {
   background: #202020;
   padding: 10px 10px 10px 15px;
   border-radius: 10px;
   display: flex;
   align-items: flex-start;
   justify-content: space-between;
   user-select: none;
   transition: 0.2s;
   border: 1px solid transparent;
}
.todo-item.done {
   filter: opacity(10);
   background: #101010;
   border: 1px solid #202020;
}
.todo-item__text {
   color: #fff;
   font-weight: 500;
   text-wrap: wrap;
   height: auto;
   max-width: 200px;
   overflow: hidden;
   text-align: left;
}
.todo-item.done .todo-item__text {
   color: #747474;
}
.todo-item__control {
   display: flex;
   gap: 10px;
}
.todo-item__svg {
   display: flex;
   align-items: center;
   justify-content: center;
   color: #fff;
   width: 24px;
   height: 24px;
   transition: 0.2s;
   cursor: pointer;
}
.todo-item.done .todo-item__svg {
   color: #747474;
}
.todo-item__svg:hover {
   transform: scale(1.1);
}
.todo-item__svg:active {
   transform: scale(0.9);
}
.todo-item__svg:hover:nth-child(2) {
   color: green;
}
.todo-item__svg:hover:nth-child(1) {
   color: red;
}
