.todo-btn {
   font-weight: 600;
   background: #95aefe;
   cursor: pointer;
   transition: 0.2s;
   border-radius: 10px;
   border: none;
   padding: 6px 12px;
   color: #101010;
}
.todo-btn:hover {
   background: #597ffc;
}
.todo-btn.disabled {
   background: #fdafaf;
}
.todo-btn.disabled:hover {
   background: #e78181;
   cursor: no-drop;
}
