.todo__form {
   display: flex;
   gap: 20px;
   width: 100%;
}
.todo__input,
.todo__btn {
   padding: 12px 25px;
   border-radius: 10px;
   border: none;
   font-weight: 500;
}
.todo__btn {
   color: #101010;
   font-weight: 600;
   background: #95aefe;
   cursor: pointer;
   transition: 0.2s;
}
.todo__btn:hover {
   background: #597ffc;
}
.todo__btn.disabled {
   background: #fdafaf;
}
.todo__btn.disabled:hover {
   background: #e78181;
   cursor: no-drop;
}
